import React, { Component } from 'react'
import { Icon } from 'antd-mobile';
import './Details.css'
const Details=(props)=>{
        
        return (
            <div className="details">
                        <img src='https://img.alicdn.com/imgextra/i1/226649068/O1CN01KohGQ42GrC4APTep9_!!226649068.jpg_640x640q80_.webp'></img>
                        <Icon type="left" size="lg"  style={{position:'relative',top:'-400px',opacity:'0.5'}} onClick={()=>props.history.push('/home')}/>
                        <div className="bot">
                                <ul>
                                        <li className='iconfont icon-dianpu' onClick={()=>props.history.push('/store')}><div id="store">店铺</div></li>
                                        <li className='iconfont icon-kefuxiaoxixinxi' onClick={()=>props.history.push('/service')}><div id="service">客服</div></li>
                                        <li className='iconfont icon-shoucang'><div id="collect">收藏</div></li>
                                        <li id="bright">
                                                <span id="bright1" onClick={()=>props.history.push('/getcart')}>加入购物车</span>
                                                <span id="bright2" onClick={()=>props.history.push('/buy')}>立即购买</span>
                                        </li>
                                </ul>
                                
                        </div> 
            </div>
        )
}
export default Details;
